body {
  background-color: #000;
  margin: 0;
  padding: 0;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
}

#container {
  grid-gap: 20px;
  text-align: center;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  align-items: center;
  margin: auto;
  padding: 20px;
  display: grid;
}

.champion {
  background: #f9f9f9;
  border-radius: 10px;
  transition: transform .3s, box-shadow .3s, filter .3s;
  overflow: hidden;
  box-shadow: 0 4px 8px #0000001a;
}

.champion:hover {
  box-shadow: 0 16px 32px #ffffff8c;
}

.image-container:hover h2 {
  opacity: 0;
}

.image-container {
  background-color: #000;
  width: 100%;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.loading {
  cursor: pointer;
  width: 100%;
  height: auto;
}

h2 {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-shadow: 2px 2px 4px #00000080;
  box-sizing: border-box;
  opacity: 1;
  transition: opacity .3 ease;
  background: #0009;
  width: 100%;
  margin: 0;
  padding: 10px;
  font-size: 16px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.abilities {
  opacity: 0;
  visibility: hidden;
  background: #0009;
  border-radius: 0 0 10px 10px;
  justify-content: center;
  align-items: center;
  padding: 10px;
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.image-container:hover .abilities {
  visibility: visible;
  opacity: 1;
}

.ability {
  cursor: pointer;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  margin: 0 10px;
  transition: transform .3s, box-shadow .3s;
}

.ability-container {
  display: inline-block;
  position: relative;
}

.tooltip {
  visibility: hidden;
  color: #fff;
  text-align: center;
  z-index: 1;
  opacity: 0;
  background-color: #333;
  border-radius: 6px;
  width: 100px;
  margin-left: -50px;
  padding: 5px;
  transition: opacity .3s;
  position: absolute;
  bottom: 125%;
  left: 50%;
}

.ability-container:hover .tooltip {
  visibility: visible;
  opacity: 1;
}

.ability-container:hover .ability {
  box-shadow: 0 0 18px 5px #fff;
}

p {
  color: #666;
  margin: 10px 0;
  font-size: 14px;
}
